export function strNoAccent(str) {
    if (!str) return '';  // V√©rifier que str est d√©fini
    const accents = {
        a: /[√†√°√¢√£√§√•]/g,
        e: /[√®√©√™√´]/g,
        i: /[√¨√≠√Æ√Ø]/g,
        o: /[√≤√≥√¥√µ√∂]/g,
        u: /[√π√∫√ª√º]/g,
        c: /[√ß]/g,
        n: /[√±]/g
    };

    for (let letter in accents) {
        str = str.replace(accents[letter], letter);
    }

    return str;
}
